.main {
  background: #0C1321;
  padding: 125px 0 180px;
  color: #fff;
  overflow: hidden;
  &__inner {
    display: flex;
    align-items: flex-end;
    gap: 12px;
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
  }
  &__img {
    position: relative;
    width: 50%;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -35px;
      width: 694px;
      height: 694px;
      background: #212F4A;
      filter: blur(200px);
      border-radius: 50%;
      pointer-events: none;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 2;
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: 1;
      right: -160px;
      bottom: -190px;
      max-width: 1660px;
      pointer-events: none;
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 105%;
    letter-spacing: -0.6px;
    color: rgba(255, 255, 255, 0.7);
  }
  &__title {
    font-size: 60px;
    line-height: 105%;
    font-weight: 700;
    letter-spacing: -1.8px;
    margin-top: 5px;
  }
  &__text {
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 140%;
      letter-spacing: -0.32px;
      color: rgba(255, 255, 255, 0.7);
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-top: 40px;
  }
  &__group-mob {
    display: none;
  }
  &__btn {
    height: 48px;
    padding: 5px 50px;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.32px;
    color: rgba(255, 255, 255, 0.7);
  }
}
.rental {
  padding: 120px 0;
  &__subtitle {
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.54px;
    margin-top: 10px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 40px 24px;
    margin-top: 40px;
  }
  &__btn {
    height: 48px;
    border: 2px solid #0C1321;
    border-radius: 8px;
    padding: 5px 40px;
    font-size: 16px;
    line-height: 105%;
    margin: 30px auto 0;
    &:hover {
      background: #0C1321;
      color: #fff;
    }
  }
}
.rental-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 12px;
  padding: 10px;
  transition: all 0.3s;
  &:hover {
    box-shadow: 4px 4px 12px 0px rgba(12, 19, 33, 0.12);
  }
  &__top {
    position: relative;
    padding-bottom: 100%;
    background: #F5F5F5;
    border-radius: 12px;
  }
  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__price {
    position: absolute;
    z-index: 2;
    left: 10px;
    bottom: 0;
    max-width: 100%;
    background: #FF0000;
    border-radius: 6px;
    padding: 5px;
    font-size: 14px;
    line-height: 105%;
    color: #fff;
    margin-right: 10px;
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 10px;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.32px;
    margin-bottom: auto;
  }
  &__btn {
    width: 100%;
    height: 48px;
    padding: 5px 10px;
    font-size: 16px;
    line-height: 105%;
    margin-top: 20px;
  }
}
.construction {
  position: relative;
  background: #0C1321;
  padding: 120px 0;
  color: #fff;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: -200px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 816px;
    height: 816px;
    background: rgba(33, 47, 74, 0.5);
    filter: blur(300px);
    border-radius: 50%;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.54px;
    margin-top: 10px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 30px 24px;
    margin-top: 40px;
  }
}
.construction-card {
  position: relative;
  &__img {
    position: relative;
    display: block;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    padding-bottom: 65%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0.00deg, rgb(12, 19, 33),rgba(12, 19, 33, 0) 100%);
      border-radius: 12px;
      pointer-events: none;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__title {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 20px 30px;
    font-size: 16px;
    line-height: 120%;
    &:hover {
      color: rgba(255, 255, 255, 0.7);
    }
  }
}
.advantages {
  padding: 120px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 24px;
  }
  &__desc {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__img {
    width: 50%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 36px;
    }
  }
  &__title {
    font-size: 42px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 30px 24px;
    margin-top: 40px;
  }
  &__item {
    min-height: 200px;
    background: #fff;
    border-radius: 12px;
    padding: 30px 20px;
    &-icon {
      width: 80px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FF0000;
      border-radius: 12px;
      img {
        max-width: 90%;
        max-height: 90%;
      }
    }
    &-title {
      font-size: 18px;
      line-height: 110%;
      letter-spacing: -0.54px;
      margin-top: 20px;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 120px / 4));
    gap: 40px;
    background: #fff;
    border-radius: 12px;
    padding: 30px 40px;
    margin-top: 40px;
  }
  &__item2 {
    &:not(:last-child) {
      border-right: 2px solid rgba(12, 19, 33, 0.7);
      padding-right: 16px;
    }
    &-title {
      font-size: 48px;
      line-height: 100%;
      font-weight: 700;
      letter-spacing: -1.44px;
    }
    &-text {
      font-size: 18px;
      line-height: 105%;
      letter-spacing: -0.54px;
      color: rgba(12, 19, 33, 0.7);
      margin-top: 5px;
    }
  }
}
.works {
  background: #fff;
  padding: 120px 0;
  &__slider {
    position: relative;
    padding-bottom: 42px;
    margin-top: 40px;
  }
  @media screen and (min-width: 993px) {
    .swiper-slide {
      margin: 0 8px 8px 0 !important;
    }
  }
  .swiper-wrapper {
    flex-direction: row;
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    height: auto;
    display: block;
    padding-bottom: 18%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 6px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 42px;
    height: 42px;
    border: 2px solid #0C1321;
    border-radius: 8px;
    opacity: 1 !important;
    pointer-events: auto !important;
    &:after {
      display: none;
    }
    &:hover {
      background: #FFC413;
      border-color: #FFC413;
    }
    svg {
      max-width: 80%;
      max-height: 80%;
    }
  }
  .swiper-button-prev {
    left: -62px;
  }
  .swiper-button-next {
    right: -62px;
  }
  .swiper-pagination {
    bottom: 0;
    &-bullet {
      width: 20px;
      height: 20px;
      background: #0C1321;
      border-radius: 20px;
      opacity: 1 !important;
    }
    &-bullet-active {
      width: 60px;
      background: #FFC413;
    }
  }
}

@media screen and (max-width: 1400px) {
  .works {
    &__slider {
      padding-left: 62px;
      padding-right: 62px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1150px) {
  .main {
    padding: 100px 0 160px;
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 40px 0 60px 0;
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 20px;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      max-width: 500px;
      width: 100%;
      margin: 0 auto;
      &:after {
        right: 50%;
        bottom: -112px;
        transform: translate(50%, 0);
      }
    }
    &__subtitle {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
    &__title {
      font-size: 34px;
      letter-spacing: -1.02px;
    }
    &__text {
      margin-top: 20px;
      p {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
    &__group {
      display: none;
    }
    &__group-mob {
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 20px;
      margin-top: 20px;
    }
    &__info {
      font-size: 14px;
      letter-spacing: -0.28px;
      br {
        display: none;
      }
    }
  }
  .rental {
    padding: 80px 0;
    &__subtitle {
      font-size: 14px;
      letter-spacing: -0.42px;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
      margin-top: 30px;
    }
  }
  .construction {
    padding: 80px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      margin-top: 30px;
    }
  }
  .construction-card {
    &__title {
      padding: 20px;
    }
  }
  .advantages {
    padding: 80px 0;
    &__wrap {
      flex-direction: column;
      gap: 30px;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      max-width: 500px;
      width: 100%;
      margin: 0 auto;
    }
    &__title {
      font-size: 28px;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      margin-top: 30px;
    }
    &__item {
      &-title {
        font-size: 16px;
        letter-spacing: -0.32px;
      }
    }
    &__items2 {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
      padding: 40px 30px;
      margin-top: 30px;
    }
    &__item2 {
      border-right: unset !important;
      &:nth-child(odd) {
        border-right: 2px solid rgba(12, 19, 33, 0.7) !important;
      }
    }
  }
  .works {
    padding: 80px 0;
    &__slider {
      padding-left: 0;
      padding-right: 0;
      padding-bottom: 50px;
      margin-top: 30px;
    }
    .swiper-slide {
      padding-bottom: 35%;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .rental {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
  .works {
    .swiper-slide {
      padding-bottom: 54%;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 80px;
    &__text {
      p {
        br {
          display: none;
        }
      }
    }
    &__img {
      max-width: 330px;
      img:nth-child(2) {
        right: -90px;
        bottom: -100px;
        max-width: 910px;
      }
    }
    &__group-mob {
      width: 100%;
    }
    &__btn {
      width: 100%;
      padding: 5px 20px;
    }
  }
  .rental {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 32px 16px;
    }
  }
  .construction {
    &__cards {
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
  .advantages {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
    &__items2 {
      grid-template-columns: 100%;
    }
    &__item2 {
      &:nth-child(odd) {
        border-right: unset !important;
      }
      &:not(:last-child) {
        border-right: unset;
        border-bottom: 2px solid rgba(12, 19, 33, 0.7);
        padding-right: 0;
        padding-bottom: 10px;
      }
    }

  }
  .works {
    &__slider {
      padding-bottom: 0;
    }
    .swiper-slide {
      padding-bottom: 109%;
    }
    .swiper-pagination {
      bottom: 20px;
      &-bullet {
        width: 8px;
        height: 8px;
        background: rgba(255, 255, 255, 0.5);
        border-radius: 50%;
        &-active {
          width: 8px;
          background: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .rental {
    &__cards {
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
}

@media screen and (max-width: 359px) {
  .advantages {
    &__items {
      grid-template-columns: 100%;
    }
  }
}