.wrapper {
  overflow: hidden;
}
.btn-yellow {
  background: #FFC413;
  border-radius: 8px;
  font-size: 16px;
  line-height: 100%;
  &:hover {
    background: #FFD65A;
  }
}
.h2 {
  font-size: 46px;
  line-height: 105%;
  letter-spacing: -1.38px;
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 28px;
    letter-spacing: -0.84px;
  }
}